*{
    margin: 0;
}
.mobile{
    display:none;
}
.header
{
    width: 100%;
    display: flex;
    align-items: center;
    height: 63px;
    border: 1px solid black;
    background-image: linear-gradient(0deg, rgba(0, 44, 111, 0.6), rgba(0, 44, 111, 0.6)), url(./../public/asset/backlong.png);
    /* background-size: cover; */

}
.card-status{
    display: none;
    position: absolute;
    top: 80%;
    left: 6%;
}
.card-status>span{
    margin-right: 5px;
    color: white;
    font-size: 1rem;
    display: flex;
}
.header>label{
position: absolute;
font-family: 'Roboto';
font-style: normal;
font-weight: 700;
font-size: 2.4vw;
line-height: 36px;
color: #FFFF;
left: 40px;
}
.body-container{
    /* margin-top:20px ; */
    
    width: 90vw;
    margin: 70px auto;
    height: max-content;
    display: flex;
    padding: 20px;
    background-color:#F3F3F3;
}
.adventure-tile{
    flex:0.2;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    /* background-repeat: repeat-y; */
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(./../public/asset/adv.png);
}
.adventure-tile>p{
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 700;
    font-size: 3vw;
    line-height: 40px;
    color: #FFFF;
}
.cards-container{
    margin-left: 20px;
    width: 100%;
    flex: 0.8;
    /* border: 1px solid black; */
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.card{
    position: relative;
    /* padding: 1px; */
    background-color: #ffffff;
    /* border-radius: 10px; */
    display: flex;
    width: 100%;
    height: 48%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    /* border:1px solid black; */
}
.card>img{
    flex: 0.2;
    max-width: 20vw;
    height: auto;
}
.card>.card-content{
    display: flex;
    flex-direction: column;
    padding: 0 10px 0 10px;
 flex:0.6;
 border-right: 1px solid rgba(96, 96, 96, 0.28);
}
.card>.card-rating{
    display: flex;
    flex-direction:column ;
    justify-content: space-around;
    align-items: center;
    padding: 5px;
    flex: 0.2;
    /* border-: 1px solid black; */
}
.cont-title{
    flex:.4;
}
.titled{
    font-size: 2vw;
    font-weight: 600;
}
.cont-title>p{
    display: flex;
}
.cont-title>p>span>.title-ic
{
    font-size: 1rem;
    margin-right: 2px;
}
.cont-title>p>span{
    font-size: 1rem;
    display: flex;
    align-items:center;
}
.cont-icon{
    flex:.25;
    padding: 5px;
    display: flex;
    border-bottom: 2px solid rgba(210, 210, 210, 0.19);
    border-top: 2px solid rgba(210, 210, 210, 0.19);
    justify-content: space-around;
}
.div-icon{
    font-size: .55rem;
    font-weight: 700;
    width: 3.2rem;
    height: 3.2rem;
    border: 1px solid black;
    padding: 5px;
    display: flex;
    flex-direction: column; 
    justify-content:center;
    align-items: center;
    background-color: #e0dada;
}
.cont-det{
    font-size: 1vw;
    overflow-x: auto;
    flex: .35;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.cont-det>span{
    padding: 2px 8px 2px 8px;
    color: #345E9C;
    border: 1px solid #9A9A9A;
    border-radius: 27px;

}
.newrat{
    display: flex;
    flex-direction: column;
}

.rat-star{
    display: flex;
    color: #FFCE31;
    
}
.card-rating>button{
    padding: 5px 7px 5px 7px;
    background-color:#218838;
    color: #ffff;
    box-shadow: none;
    cursor: pointer;
}
.rat-star>.ratingno{
    color: #ffff;
    padding: 0px 5px 0px 5px;
    margin-left: 5px;
    /* border: 1px solid black; */
    background-color: #FFCE31;
}
@media (max-width:1260px) and (min-width:800px) {
    .div-icon{
        width: 2rem;
        height: 2rem;
        font-size: .45rem;
    }
    .titled{
        font-size:1.6vw
    }
}
@media only  screen and (max-width:800px) {
    .wide{
        display: none;
    }
    .header>label{
        font-size: 7vw;
    }
    .mobile{
        display: flex;
    }
    .card-status{
        display: flex;
        top:40%;
    }
    .newratmobile{
        padding-top: 10px;
        display: flex;
        border-top :0.5px solid rgba(0, 0, 0, 0.26);
        justify-content: space-around;
        /* align-items: center; */
    }
    .adventure-tile>p{
        font-size: 7vw;
    }
    .body-container{
        display: block;
        width: 90vw;
        flex-direction: column;
    }
    .card{
        width: 80%;
        flex-direction: column;
        height: 60vh;
        margin: 20px auto;
        border-radius: 10px 0 0 0;
    }
    .card>.card-rating{
        justify-content: space-between;
        align-items: normal;
        padding: 0;
        
    }
    .card-rating>button{
        /* padding: 0; */
        width: 100%;
    }
    .cards-container
    {
        margin-left: 0px;
    }
    .card>img{
    flex: 0.5;
    max-width: 100%;
    height: 50%;
    
    
    }
    .cont-title>.title-icons{
        display:none;
    }
    .card>.card-content{
        
    flex:0.3;
    
    }
    .card>.card-rating{
        flex: 0.2;
    }
    .titled{
        font-size: 4vw;
        font-weight: 600;
    }
    .cont-det{
        font-size: 2vw;
    }
    .adventure-tile{
        height: 40vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background-size: cover;
        /* background-repeat: repeat-y; */
        background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(./../public/asset/advent.png);
    }
}